

import {computed, defineComponent} from "vue";

export default defineComponent({
  name: "ActionLink",
  props: {
    title: {
      type: String,
      default: "Action"
    },
    enabled: {
      type: Boolean,
      default: true
    },
    running: {
      type: Boolean,
      default: false
    }
  },
  emits: ['action'],
  setup(props) {

    const actionDisabled = computed(() => {
      return props.running || !props.enabled
    })

    return { actionDisabled }
  }
})

