

import {computed, defineComponent} from "vue";
import {routeManager} from "@/router";

export default defineComponent({
  name: "TopicLink",

  props: {
    topicId: String,
    showExtra: {
      type: Boolean,
      default: false
    }
  },

  setup(props) {
    const topicRoute = computed(() => props.topicId ? routeManager.makeRouteToTopic(props.topicId) : null)
    return { topicRoute }
  }
});

