

import {ComputedRef, defineComponent, inject, PropType, Ref} from "vue";
import {Transaction} from "@/schemas/HederaSchemas";
import TransactionSummary from "@/components/transaction/TransactionSummary.vue";
import TimestampValue from "@/components/values/TimestampValue.vue";
import TransactionLabel from "@/components/values/TransactionLabel.vue";
import {makeTypeLabel} from "@/utils/TransactionTools";
import {routeManager} from "@/router";
import {ORUGA_MOBILE_BREAKPOINT} from "@/App.vue";
import {TransactionTableControllerXL} from "@/components/transaction/TransactionTableControllerXL";
import EmptyTable from "@/components/EmptyTable.vue";

export default defineComponent({
  name: "TransactionTable",

  components: {TransactionSummary, TimestampValue, TransactionLabel, EmptyTable },

  props: {
    narrowed: Boolean,
    controller: {
      type: Object as PropType<TransactionTableControllerXL>,
      required: true
    }
  },

  setup(props) {
    const isTouchDevice = inject('isTouchDevice', false)
    const isMediumScreen = inject('isMediumScreen', true)

    const handleClick = (t: Transaction) => {
      routeManager.routeToTransaction(t)
    }

    return {
      isTouchDevice,
      isMediumScreen,
      transactions: props.controller.rows as ComputedRef<Transaction[]>,
      loading: props.controller.loading as ComputedRef<boolean>,
      total: props.controller.totalRowCount as ComputedRef<number>,
      currentPage: props.controller.currentPage as Ref<number>,
      onPageChange: props.controller.onPageChange,
      perPage: props.controller.pageSize as Ref<number>,
      handleClick,
      makeTypeLabel,
      ORUGA_MOBILE_BREAKPOINT,
    }
  }
})

