

import {computed, defineComponent} from "vue";
import {routeManager} from "@/router";

export default defineComponent({
  name: "BlockLink",

  props: {
    blockNumber: Number,
  },

  setup(props) {
    const blockRoute = computed(() => props.blockNumber ? routeManager.makeRouteToBlock(props.blockNumber) : null)
    return { blockRoute }
  }
});

