

import {computed, defineComponent, inject, ref} from "vue";
import {initialLoadingKey} from "@/AppKeys";

export default defineComponent({
  name: "BlobValue",
  components: {},
  props: {
    blobValue: String,
    showNone: {
      type: Boolean,
      default: false
    },
    base64: {
      type: Boolean,
      default: false
    },
    pretty: {
      type: Boolean,
      default: false
    },
    limitingFactor: Number
  },

  setup(props) {
    const isMediumScreen = inject('isMediumScreen', true)
    const windowWidth = inject('windowWidth', 1280)
    const isURL = computed(() => {
      let result: boolean
      if (props.blobValue) {
        try {
          const url = new URL(props.blobValue)
          result = url.protocol == "http:" || url.protocol == "https:"
        } catch {
          result = false
        }
      } else {
        result = false
      }
      return result
    })

    const jsonValue = computed(() => {
      let result
      if (decodedValue.value && props.pretty) {
        try {
          result = JSON.parse(decodedValue.value)
        } catch (e) {
          result = null
        }
      } else {
        result = null
      }
      return result
    })

    const decodedValue = computed(() => {

      const base64regex = /^([0-9a-zA-Z+/]{4})*(([0-9a-zA-Z+/]{2}==)|([0-9a-zA-Z+/]{3}=))?$/;

      let result: string
      if (props.blobValue) {
        if (props.base64 && base64regex.test(props.blobValue)) {
          try {
            result = Buffer.from(props.blobValue, 'base64').toString()
          } catch {
            result = props.blobValue
          }
        } else {
          result = props.blobValue
        }
      } else {
        result = ""
      }
      return result
    })

    const initialLoading = inject(initialLoadingKey, ref(false))

    return {
      isMediumScreen,
      windowWidth,
      isURL,
      jsonValue,
      decodedValue,
      initialLoading
    }
  }
})

