

import {computed, defineComponent, inject, PropType, ref} from "vue";
import {initialLoadingKey} from "@/AppKeys";
import {routeManager} from "@/router";
import {NodeRegistry} from "@/components/node/NodeRegistry";

export default defineComponent({
  name: "AccountLink",

  props: {
    accountId: String as PropType<string|null>,
    showExtra: {
      type: Boolean,
      default: false
    },
    showNone: {
      type: Boolean,
      default: false
    },
    noAnchor: {
      type: Boolean,
      default: false
    },
    nullLabel: {
      type: String,
      default: "O"
    }
  },

  setup(props) {
    const extra = computed(() => {
      return NodeRegistry.getDescription(ref(null), ref(props.accountId??null)) ?? ""
    })

    const accountRoute = computed(() => {
      return props.accountId ?  routeManager.makeRouteToAccount(props.accountId) : null
    })

    const initialLoading = inject(initialLoadingKey, ref(false))

    return { extra, accountRoute, initialLoading }
  }
});

