

import {defineComponent} from "vue";

export default defineComponent({
  name: "ModalDialog",
  components: {},
  props: {
    showDialog: {
      type: Boolean,
      default: false
    },
    iconClass: String
  },

  setup(props, context) {
    const handleClose = () => {
      context.emit('update:showDialog', false)
      context.emit('onClose')
    }
    return { handleClose }
  }
});

