

import {defineComponent} from "vue";

export default defineComponent({
  name: "ConfirmDialog",
  components: {},
  props: {
    showDialog: {
      type: Boolean,
      default: false
    },
    mainMessage: String,
    extraMessage: String,
  },

  setup(props, context) {

    const handleCancel = () => {
      context.emit('update:showDialog', false)
      context.emit('onCancel')
    }

    const handleConfirm = () => {
      context.emit('update:showDialog', false)
      context.emit('onConfirm')
    }

    return {
      handleCancel,
      handleConfirm,
    }
  }
});

